import React, { useEffect, useRef, useState } from "react";
import { AiOutlinePlus } from "react-icons/ai"
import Todo from "./Todo";
import { db } from "./firebase";
import { query, collection, onSnapshot, querySnapshot, updateDoc, doc, addDoc, deleteDoc } from "firebase/firestore";
function App() {
  const [todos, setTodos] = useState([]);
  const [input, setInput] = useState("")
  // Crud
  // Create
  const createTodo = async(e) => {
    e.preventDefault();
    if (input === "") {
      alert("Please enter valid Todo")
      return
    }
    await addDoc(collection(db, 'todoApp'), {
      Text: input,
      Completed: false,
    })
    setInput("")
  }
  // Read from firebase
  useEffect(() => {
    const q = query(collection(db, 'todoApp'));
    const unSubscribe = onSnapshot(q, (querySnapshot) => {
      let todosArr = [];
      querySnapshot.forEach((doc) => {
        todosArr.push({ ...doc.data(), id: doc.id }); // Access the text property using doc.data()
      });
      setTodos(todosArr);
      console.log(todos[0]);
    });
    return () => unSubscribe();
  }, []);

  // Update

  const toggleCompleted = async (todo) => {
    await updateDoc(doc(db, 'todoApp', todo.id), {
      Completed: !todo.Completed
    })
  }
  // Delete

  const deleteTodo = async (id) => {
    await deleteDoc(doc(db, 'todoApp', id))
  }
  return (<div className="w-screen h-screen bg-gradient-to-b from-[#5D2A42]  via-[#FCB1A6] to-[#FFF9EC] pt-[4rem]">
    <div className="bg-[#e8abce] w-[50%] mx-auto shadow-lg rounded-xl pb-[2rem]">
      <h1 className="text-center text-6xl py-5 font-medium">Todo App</h1>
      <form onSubmit={createTodo} className="w-[80%] mx-auto flex flex-wrap justify-between items-center">
        <input type="text" placeholder="Add Todos" className="w-[84%] py-3 px-3 text-left focus:outline-none rounded-md mb-5 text-lg" value={input} onChange={(e) => { setInput(e.target.value) }} />
        <button className="w-[15%] bg-[#965573] text-[#FFF9EC] mt-[-1.2rem] rounded-md py-[1px]"><AiOutlinePlus size={50} className="mx-auto" /></button>
      </form>
      <ul>
        {todos.map((todo) => {
          return <Todo todo={todo} key={todo.id} toggleCompleted={toggleCompleted} deleteTodo={deleteTodo} />
        })}
      </ul>
      {
        todos.length<1 ? "" :   <div className="w-[50%] mx-auto pt-3 text-xl text-center"> You have {todos.length} Todos</div>
    }
    </div>

  </div>)
}

export default App;
