import React from 'react';
import {AiOutlineDelete} from "react-icons/ai"

export default function Todo({todo, toggleCompleted,deleteTodo}) {
  return (
      <>
          <li className={`flex flex-wrap justify-between items-center w-[80%] mx-auto  py-4 px-3 mb-4 ${todo.Completed ? "bg-[#efeded] text-[#8a8a8994] line-through": "bg-[#f4e5f894]"}`}>
              <div>
                  <input type="checkbox" className='mr-3 w-[1.2rem] h-[1.2rem]' checked={todo.Completed ? "checked" : ""}
                      onChange={() => toggleCompleted(todo)} />
                  <span className='text-xl' onClick={() => toggleCompleted(todo)}>{todo.Text}
                  </span>
              </div>
              <div onClick={()=>deleteTodo(todo.id)}>
                  <AiOutlineDelete size={35} className='cursor-pointer'/>
              </div>

          </li>
          
          </>
  )
}
